body {
  // background: $main-background-color;
  // background-repeat: no-repeat;
  // height: ;
  background: $main-background-color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}

a {
  text-decoration: none;
  color: $font-color;
  &:link {
    text-decoration: none;
  }
}


::placeholder {
  color: $base-color;
}

.router {
  position: relative;
  padding-top: 4rem;
}
.title {
  font-size: $large-font-size;
  color: $font-color;
}

.blur {
  width: 100%;
  height: 100vh;
  // border: 5px solid red;
  position: fixed;
  background-color: $second-base-color;
  top: 0;
  left: 0;
  filter: blur(1px);
  // display: none;
  opacity: .3;
}

.background {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: $main-background-color;
  z-index: -2;
}

.glass {
  width: 100%;
  height: 100%;
  position: absolute;
  background: $background-color;
  left: 0;
  top: 0;
  // filter: contrast(1);
  // filter: blur(10px);
  // filter: blur(73px);
  opacity: 0.6;
  z-index: -1;
}
.background__image {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  filter: blur(1rem);
  
  .background--image {
    width: 100%;
    height: inherit;
    // object-fit: cover;
    position: absolute;
    left: 50%;
    bottom: -35%;
    transform: translateX(-50%);
    opacity: .4;
    // opacity: .2;
  }
}

.main {
  // display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 100vh;
  border-bottom: 2px solid $base-color;
  margin-bottom: 1rem;
  position: relative;
  // padding: 10rem;
  // position: fixed;
  // top: 0;
  // left: 0;
  .btn {
    margin-bottom: 7rem;
    // position: absolute;
    // bottom: 10%;
    // left: 50%;
    // transform: translateX(-50%);
  }
}

.gainer--background {
  background-color: $gainer-color;
  color: $font-color;
}
.gainer--color {
  color: $gainer-color;
}
.loser--background {
  background-color: $loser-color;
  color: $font-color;
}
.loser--color {
  color: $loser-color;
}

.not--available {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40vh;

  h2 {
    font-size: $regular-font-size;
    color: $second-font-color;
  }
}

.active {
  color: $active-color;
  // border-bottom: 1px solid $border-color;
  margin-left: 2rem;
}

.active--2 {
  color: $active-color;
}

.layout {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: $dark-background;
  z-index: 2;
  opacity: .4;

  div {
    width: inherit;
    height: inherit;
    background-color: $second-base-color;
    filter: blur(1rem);
  }
}

.confirmation__screen {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  // background-color: ;

  .confirmation__content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50rem;
    height: 23rem;
    background-color: #00808852;
    border: 1px solid #00B4BF;
    border-radius: .5rem;
    
    h2 {
      font-size: $extra-large-font-size;
      color: $active-color;
    } 
    h3 {
      font-size: $large-font-size;
      color: $font-color;
    } 

    .confirmation__buttons{
      display: flex;
      justify-content: space-around;
      margin-top: 5rem;
      .btn {
        position: revert;
        bottom: none;
        right: none;
      }
      width: 100%;
      // border: 1px solid red;
    }

  }
  .confirmation--layout {
    width: 100%;
    height: 100vh;
    background-color: $dark-background;
    position: absolute;
    top: 0;
    left: 0;
    opacity: .7;
    z-index: -1;
  }
}

::-webkit-scrollbar {
  width: 1rem;
  opacity: .5;
  border-radius: 1rem;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px $border-color; 
  // border-radius: 1rem;
  background: $second-base-color;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: $active-color; 
  border-radius: 1rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $base-color; 
}